<template>
    <div
        v-if="typeStartScreencast === 'camera' || operation === 'camera'"
        class="main-camera"
    >
        <video
            muted="muted"
            autoplay="autoplay"
            :srcObject.prop="videoStream"
            @loadedmetadata="loadedMetaData"
        />
    </div>
    <div
        v-else
        class="main-capture"
    >
        <img
            alt="logo"
            class="logo"
            src="@/images/logo/nimble_capture_display_media.svg"
        >
        <div
            v-if="videoStream?.active"
            class="main-capture-content"
        >
            <div class="title">
                Keep this tab open while recording
            </div>
            <div class="destruction">
                We will automatically close this tab when your record is fully uploaded
            </div>
            <button
                class="button-stop-record"
                type="button"
                @click="stopMediaRecorder"
            />
            <div>
                <div class="record-time">
                    {{ videoTime }}
                </div>
            </div>
        </div>
        <div
            v-if="!videoStream?.active && typeStartScreencast === 'desktop'"
            class="main-desktop-content"
        />
        <video
            muted="muted"
            style="opacity: 0; position: absolute"
            autoplay="autoplay"
            :srcObject.prop="videoStream"
            @loadedmetadata="loadedMetaData"
        />
    </div>
</template>

<script>
// import ContentHelper from '@/content/ContentHelper';
import LS from '@/lib/LocalStorage';
import Screencast from '@/lib/screencast';
import nscExt from '@/lib/extension';

import nscCore from '@/lib/core';
// import ContentWatermark from '@/content/ContentWatermark';
import { useStore } from 'vuex';
import { computed } from 'vue';
import browserAction from '@/content/BrowserAction';
import VideoRecorder from '@/lib/VideoRecorder';
import AfterCapture from '@/lib/AfterCapture';
import ContentHelper from '@/content/ContentHelper';

let isRecord = false;
const IsVideoStream = false;
let audioStream = null;
// let audioCloneStream = null;
let videoStream = null;
let mediaRecorder = null;
let timeStart = null;
let timePause = null;
let intervalVideoTime = null;
const mediaRecorderChunks = [];

const screencastUpdateStatus = async () => {
  const typeStartScreencast = await LS.getItem('videoRecordType');

  if (typeStartScreencast === 'camera') {
    // await ContentWatermark.init(true);
  } else {
    await nscCore.sendMessage({
      operation: 'screencastUpdateStatusBG',
      status: videoStream?.active,
      state: mediaRecorder?.state
    });
  }
};

const getTime = () => {
  const date = Date.now();

  timeStart += (timePause ? date - timePause : 0);
  timePause = timePause ? date : null;
  return timeStart ? date - timeStart : 0;
};

const closeFrame = value => {
  try {
    window.parent.postMessage(JSON.stringify({ action: 'nscFrameRemove', value }), '*');
  } catch (err) {
    console.error(`Error: ${err}`, err);
  }
};

const stopMediaRecorder = async () => {
  if (!isRecord) {
    return;
  }
  isRecord = false;
  console.log('stop');

  await browserAction.setDefault();

  window.onbeforeunload = () => {};

  if (audioStream?.active) {
    audioStream.getTracks()
      .forEach(track => {
        track.stop();
      });
  }

  if (videoStream?.active) {
    videoStream.getTracks()
      .forEach(track => {
        track.stop();
      });
  }

  clearInterval(intervalVideoTime);
  const blob = new Blob(mediaRecorderChunks, { type: 'video/webm' });
  const rootDir = await navigator.storage.getDirectory();

  LS.setItem('fileVideoName', 'video');
  LS.setItem('fileVideoFormat', 'webm');

  const fileHandle = await rootDir.getFileHandle('video.webm', { create: true });
  const writable = await fileHandle.createWritable();
  await writable.write(blob);
  await writable.close();

  console.log('content written to file', blob);

  screencastUpdateStatus(); // TODO:   

  await nscExt.createPageInfo();
  await AfterCapture.run('screencast');
};

const startMediaRecorder = async () => {
  timeStart = Date.now();

  mediaRecorder = new MediaRecorder(videoStream, { mimeType: 'video/webm; codecs=vp8' });
  // mediaRecorder = new MediaRecorder(this.videoStream, { mimeType: 'video/webm; codecs=h264' });

  if (IsVideoStream) {
    await browserAction.setLoading();
  } else {
    await browserAction.setRec();
  }

  mediaRecorder.ondataavailable = async e => {
    mediaRecorderChunks.push(e.data);

    if ((IsVideoStream || await nscExt.getTrial()) && Date.now() - timeStart > nscExt.trialVideoDuration) {
      await stopMediaRecorder();
    }
  };

  mediaRecorder.onstop = stopMediaRecorder;
  mediaRecorder.start(1000);

  isRecord = true;
  await screencastUpdateStatus();
};

const getUrlParam = (name, url) => {
  const urlObj = new URL(url || window.location.href);
  return urlObj.searchParams.get(name);
};

export default {
  name: 'DisplayMedia',
  inject: ['i18n'],
  setup() {
    const store = useStore();

    (async () => {
      await store.dispatch('nimbus/isAuth');
    })();

    return {
      // isNimbusAuth: computed(() => store.state.nimbus.isAuth),
    };
  },
  data() {
    return {
      operation: getUrlParam('operation'),
      typeStartScreencast: 'tab',
      IsMediaCamera: false,
      IsMediaMicrophone: false,
      IsVideoStream: false,
      videoStream: null,
      videoTime: ''
    };
  },
  watch: {
    // whenever question changes, this function will run
    videoStream(value) {
      videoStream = value;
    },
  },
  created() {
    // document.title = chrome.i18n.getMessage('appNameMini');
  },
  async mounted() {
    this.typeStartScreencast = (await LS.getItem('videoRecordType')) || 'tab';
    this.IsMediaCamera = (await LS.getItem('videoCameraEnable')) || false;
    this.IsMediaMicrophone = (await LS.getItem('videoMicSoundEnable')) || false;
    // this.IsVideoStream = (await LS.getItem('IsVideoStream')) || false;

    intervalVideoTime = setInterval(() => {
      const date = new Date(getTime());
      const m = date.getUTCMonth();
      const d = date.getUTCDate() - 1;
      const h = date.getUTCHours();
      const M = date.getUTCMinutes();
      const s = date.getUTCSeconds();

      let timeStr = '';

      if (m > 0) timeStr += `${(`0${m}`).slice(-2)}:`;
      if (d > 0) timeStr += `${(`0${d}`).slice(-2)}:`;
      timeStr += `${(`0${h}`).slice(-2)}:`;
      timeStr += `${(`0${M}`).slice(-2)}:`;
      timeStr += (`0${s}`).slice(-2);

      this.videoTime = timeStr;
    }, 1000);

    switch (this.operation) {
      case 'getUserMediaCamera': {
        try {
          this.videoStream = await navigator.mediaDevices.getUserMedia({
            video: true
          });
          LS.setItem('videoCameraEnable', true);
          this.videoStream.getTracks()[0].stop();
        } catch (err) {
          console.error(`Error: ${err}`);
        }
        closeFrame();
        break;
      }
      case 'getUserMediaMicrophone': {
        try {
          this.videoStream = await navigator.mediaDevices.getUserMedia({
            audio: true,
            echoCancellation: true,
            noiseSuppression: true
          });
          LS.setItem('videoMicSoundEnable', true);
          this.videoStream.getTracks()[0].stop();
        } catch (err) {
          console.error(`Error: ${err}`);
        }
        closeFrame();
        break;
      }
      case 'camera': {
        const cameraInputDeviceId = await LS.getItem('selectedVideoCamera') || 'default';
        let constraintsCamera = { video: true };

        if (cameraInputDeviceId !== 'default') {
          constraintsCamera = {
            video: {
              deviceId: {
                ideal: cameraInputDeviceId
              }
            }
          };
        }
        try {
          this.videoStream = await Screencast.getUserMedia(constraintsCamera);
        } catch (err) {
          console.error(`Error video: ${err}`, err);
          window.parent.postMessage(JSON.stringify({
            action: 'nscFrameVideoSize',
            value: false
          }), '*');
        }
        break;
      }
      case 'screenshot': {
        const { streamId } = await Screencast.getDesktopStreamId();
        const constants = {
          audio: false,
          video: {
            mandatory: {
              chromeMediaSource: 'desktop',
              chromeMediaSourceId: streamId
            }
          }
        };

        await nscCore.sendMessage({ operation: 'minimizedLastCreateWindow' });
        await nscCore.setTimeout(100);

        try {
          this.videoStream = await navigator.mediaDevices.getUserMedia(constants);
        } catch (err) {
          console.error(`Error: ${err}`);
          window.close();
        }

        if (this.videoStream) {
          const canvas = document.createElement('canvas');
          const ctx = canvas.getContext('2d');
          const partsImage = [];
          const partsPosition = [];
          const video = document.createElement('video');
          video.srcObject = this.videoStream;
          await video.play();
          canvas.width = video.videoWidth;
          canvas.height = video.videoHeight;
          ctx.drawImage(video, 0, 0);
          this.videoStream.getTracks()[0].stop();
          video.remove();

          const format = await nscExt.imageFormat();
          const quality = await nscExt.imageQuality();
          const dataUrl = canvas.toDataURL(`image/${format}`, quality);

          partsImage.push(dataUrl);
          partsPosition.push({
            x: 0,
            y: 0,
            w: canvas.width,
            h: canvas.height
          });

          LS.setItem('imageFileParts', JSON.stringify(partsImage), true);
          LS.setItem('imageFileInfo', JSON.stringify({
            w: canvas.width,
            h: canvas.height,
            z: 1,
            parts: partsPosition,
            isMobile: ContentHelper.is_mobile
          }), true);

          nscCore.sendMessageSync({ operation: 'openPageDoneScreenshot' });
          nscCore.sendMessageSync({ operation: 'closeLastCreateWindow' });
        }
        break;
      }
      case 'screencast': {
        window.onbeforeunload = e => {
          e.preventDefault();
          e.returnValue = 'Are you sure you want to exit?';
        };

        const [tab] = await nscCore.tabQuery({ active: true, currentWindow: true });
        LS.setItem('lastActiveVideoTabId', tab.id);

        if (this.typeStartScreencast === 'camera') {
          const lastCreateTabId = await LS.getItem('lastCreateTabId');
          await nscCore.tabUpdate(lastCreateTabId, { selected: true });

          const cameraInputDeviceId = await LS.getItem('selectedVideoCamera') || 'default';
          let constraintsCamera = { video: true };

          if (cameraInputDeviceId !== 'default') {
            constraintsCamera = {
              video: {
                deviceId: {
                  ideal: cameraInputDeviceId
                }
              }
            };
          }
          try {
            this.videoStream = await Screencast.getUserMedia(constraintsCamera);
          } catch (err) {
            console.error(`Error video: ${err}`, err);
            // window.close();
          }
        } else {
          // const lastCreateTabId = await LS.getItem('lastCreateTabId');
          // await nscCore.tabUpdate(lastCreateTabId, { selected: true });

          try {
            const {
              streamId,
              options
            } = await Screencast.getDesktopStreamId(['audio', 'tab', 'screen', 'window']);

            const videoResolution = await VideoRecorder.getResolution();

            const constraints = {
              video: {
                mandatory: {
                  chromeMediaSource: 'desktop',
                  chromeMediaSourceId: streamId,
                  maxFrameRate: 30,
                  maxWidth: videoResolution.width,
                  maxHeight: videoResolution.height
                }
              }
            };

            if (options.canRequestAudioTrack) {
              constraints.audio = {
                mandatory: {
                  chromeMediaSource: 'desktop',
                  chromeMediaSourceId: streamId
                }
              };
            }

            this.videoStream = await Screencast.getUserMedia(constraints);
          } catch (e) {
            window.close();
          }
        }

        await nscCore.sendMessage({ operation: 'minimizedLastCreateWindow' });
        await nscCore.setTimeout(100);

        const IsMediaMicrophone = await LS.getItem('videoMicSoundEnable') || false;

        if (IsMediaMicrophone) {
          const audioInputDeviceId = await LS.getItem('selectedMicrophone') || 'default';
          let constraintsAudio = { audio: true };

          if (audioInputDeviceId !== 'default') {
            constraintsAudio = {
              audio: {
                deviceId: {
                  ideal: audioInputDeviceId
                }
              }
            };
          }

          try {
            audioStream = await Screencast.getUserMedia(constraintsAudio);
          } catch (err) {
            console.error(`Error audio: ${err}`, err);
            // window.close();
          }

          if (audioStream) {
            const mixedStream = new MediaStream();
            const mixedAudioStream = await Screencast.getMixedAudioStream([audioStream, this.videoStream]);
            mixedAudioStream.getTracks().forEach(track => mixedStream.addTrack(track));
            videoStream.getTracks().forEach(track => mixedStream.addTrack(track));
            this.videoStream = mixedStream;

            // this.videoStream.addTrack(audioStream.getAudioTracks()[0]);
          }
        }

        const videoCountdown = await LS.getItem('videoCountdown');
        if (videoCountdown) {
          await nscExt.setExtensionBadge(videoCountdown);
        }

        this.videoStream.getVideoTracks()
          .forEach(track => {
            track.onended = stopMediaRecorder;
          });

        await this.startMediaRecorder();

        break;
      }
      default:
        break;
    }

    chrome.runtime.onMessage.addListener((request, _sender) => {
      // console.log('request display', JSON.stringify(request));

      switch (request.operation) {
        case 'startDesktopScreencast':
          (async function startScreencast() {
            await this.startMediaRecorder();
          }());
          break;
        default:
          break;
      }
    });
  },
  methods: {
    async startMediaRecorder() {
      timeStart = Date.now();

      mediaRecorder = new MediaRecorder(this.videoStream, { mimeType: 'video/webm; codecs=vp8' });
      // mediaRecorder = new MediaRecorder(this.videoStream, { mimeType: 'video/webm; codecs=h264' });

      if (this.IsVideoStream) {
        await browserAction.setLoading();
      } else {
        await browserAction.setRec();
      }

      mediaRecorder.ondataavailable = async e => {
        mediaRecorderChunks.push(e.data);

        if ((this.IsVideoStream || await nscExt.getTrial()) && Date.now() - timeStart > nscExt.trialVideoDuration) {
          await stopMediaRecorder();
        }
      };

      mediaRecorder.onstop = stopMediaRecorder;
      mediaRecorder.start(1000);

      isRecord = true;
      await screencastUpdateStatus();
    },
    stopMediaRecorder,
    async loadedMetaData(e) {
      if (this.operation === 'camera') {
        window.parent.postMessage(JSON.stringify({
          action: 'nscFrameVideoSize',
          value: { w: e.target.videoWidth, h: e.target.videoHeight }
        }), '*');
      }
    }
  }
};

chrome.runtime.onMessage.addListener((request, sender, sendResponse) => {
  // console.log('request display 222222222', JSON.stringify(request));

  switch (request.operation) {
    case 'startDesktopScreencast':
      (async function startScreencast() {
        await startMediaRecorder();
      }());
      break;
    case 'stopScreencast':
      (async function stopScreencast() {
        if (mediaRecorder) {
          mediaRecorder.stop();

          if (videoStream) {
            videoStream.getTracks()
              .forEach(track => {
                track.stop();
              });
          }
        }
      }());
      break;
    case 'pauseScreencast':
      (async function pauseScreencast() {
        if (mediaRecorder && mediaRecorder.state === 'recording') {
          timePause = Date.now();
          mediaRecorder.pause();
          await browserAction.setPause();
        } else {
          timePause = null;
          mediaRecorder.resume();
          await browserAction.setRec();
        }

        await screencastUpdateStatus();
      }());
      break;
    case 'getScreencastStatus': {
      const data = {
        time: getTime(),
        status: isRecord,
        state: mediaRecorder?.state
      };
      console.log('getScreencastStatus', data);
      sendResponse(data);
      break;
    }
    default:
      break;
  }

  return true;
});
</script>

