import nscCore from '@/lib/core';
import nscExt from '@/lib/extension';
import LS from '@/lib/LocalStorage';

export default class AfterCapture {
  static async run(type, action = null) {
    let actionQuery = `${typeof action === 'string' ? `?action=${action}` : ''}`;
    let enableEdit = await LS.getItem('enableEdit');

    console.log('AfterCapture', type, action, enableEdit);
    if (type === 'screencast') {
      enableEdit = 'edit';
    }

    // eslint-disable-next-line default-case
    switch (action) {
      case 'google':
      case 'dropbox':
      case 'slack':
      case 'quick':
      case 'print':
      case 'pdf':
        return this.handleOpenUploadPage(type, actionQuery);
      case 'edit':
        actionQuery = `?action=${enableEdit}`;
        break;
      case 'download':
        return this.handleSaveFile(type);
      case 'blank':
        return nscExt.openEditPage(`${type}${actionQuery}`);
    }

    actionQuery = `${typeof enableEdit === 'string' ? `?action=${enableEdit}` : ''}`;

    // eslint-disable-next-line default-case
    switch (enableEdit) {
      case 'google':
      case 'dropbox':
      case 'done':
        return this.handleOpenUploadPage(type, actionQuery);
      case 'edit':
        return nscExt.openEditPage(`${type}${actionQuery}`);
      case 'save':
        return this.handleSaveFile(type);
    }

    return false;
  }

  static handleOpenUploadPage(type, actionQuery = '') {
    console.log('handleOpenUploadPage', type.replace('-editor', ''));
    return nscExt.openEditPage(`${type.replace('-editor', '')}${actionQuery}`);
  }

  static async handleSaveFile(type) {
    console.log('handleSaveFile', type);
    // const file = '';

    if (type === 'screenshot' || type === 'screenshot-editor') {
      const parts = await nscExt.imageFileParts();
      const info = await nscExt.imageFileInfo();
      const format = await nscExt.imageFormat();

      const response = await nscCore.sendTabMessage(null, {
        operation: 'getScreenshotDetails',
        info,
        format,
        parts
      });

      if (response) {
        return this.downloadFile({ url: response.url, fileName: response.fileName }, true);
      //   const blob = await fetch(response.url).then(r => r.blob());
      //
      //   file = {
      //     url: response.url,
      //     blob,
      //     size: blob.size,
      //     name: response.fileName,
      //     comment: ''
      //   };
      }
    }

    // if (!file) {
    return false;
    // }
  }

  static async downloadFile({ url, fileName }, isAuto = false, window = null) {
    console.log('downloadFile', url, fileName);
    if (!url || !fileName) {
      return;
    }

    if (window) {
      window.onbeforeunload = () => {};
    }

    if (isAuto) {
      await chrome.downloads.download({ url, filename: fileName, saveAs: true });
      if (window) {
        window.close();
      }
    } else {
      const saveAs = await nscExt.enableSaveAs();
      await chrome.downloads.download({ url, filename: fileName, saveAs });
    }
  }
}
