import LS from '@/lib/LocalStorage';

export default class VideoRecorder {
  static async getVideoResolution(videoResolution, activeTab) {
    // eslint-disable-next-line default-case
    switch (videoResolution) {
      case 'auto':
        if (activeTab) videoResolution = { width: activeTab.width, height: activeTab.height };
        // eslint-disable-next-line no-restricted-globals
        else videoResolution = { width: 4096, height: 3072 };
        break;
      case 'qhd':
        videoResolution = { width: 854, height: 480 };
        break;
      case 'hd':
        videoResolution = { width: 1280, height: 720 };
        break;
      case 'fullhd':
        videoResolution = { width: 1920, height: 1080 };
        break;
      case '2k':
        videoResolution = { width: 2560, height: 1440 };
        break;
      case '4k':
        videoResolution = { width: 3840, height: 2160 };
        break;
    }
    return videoResolution;
  }

  static async getResolution(tab = null) {
    const videoRecordIsStream = !!await LS.getItem('videoRecordIsStream');
    const resolution = videoRecordIsStream ? 'hd' : await LS.getItem('videoResolution');
    return VideoRecorder.getVideoResolution(resolution, tab);
  }
}
