import nscCore from '@/lib/core';
import nscExt from '@/lib/extension';
import AfterCapture from '@/lib/AfterCapture';
import LS from '@/lib/LocalStorage';

if (typeof MediaStream !== 'undefined' && !('stop' in MediaStream.prototype)) {
  MediaStream.prototype.stop = () => {
    console.log(this);
    this.getAudioTracks().forEach(track => {
      track.stop();
    });

    this.getVideoTracks().forEach(track => {
      track.stop();
    });
  };
}

export default class Screencast {
  static listVideoGuideEvents = [];
  static captureListVideoGuideEvents = [];
  static captureVideoGuideProcess = false;

  static async getTabStreamId(tab) {
    return new Promise((resolve, reject) => {
      chrome.tabCapture.getMediaStreamId({ /* consumerTabId: tab.id, */targetTabId: tab.id }, async streamId => {
        if (!streamId) {
          reject(Error('tabCapture no search streamId'));
        } else {
          resolve({ streamId });
        }
      });
    });
  }

  // eslint-disable-next-line default-param-last
  static async getDesktopStreamId(sources = ['tab', 'screen', 'window'], targetTab) {
    return new Promise((resolve, reject) => {
      chrome.desktopCapture.chooseDesktopMedia(sources, targetTab, async (streamId, options) => {
        if (!streamId) {
          reject(Error('desktopCapture no search streamId'));
        } else {
          console.log(streamId, options);
          resolve({ streamId, options });
        }
      });
    });
  }

  static async getUserMedia(constraints = { video: true, audio: true }) {
    return navigator.mediaDevices.getUserMedia(constraints);
  }

  static async saveVideoGuideEventCapture() {
    // console.log('saveEventCapture step', this.captureListVideoGuideEvents);

    // const canvas = document.createElement('canvas');
    // const ctx = canvas.getContext('2d');

    try {
      const dataUrl = await nscExt.captureVisibleTab();
      const imageBlob = nscExt.dataURItoBlob(dataUrl);
      const image = await createImageBitmap(imageBlob);
      const canvas = new OffscreenCanvas(image.width, image.height);
      const ctx = canvas.getContext('2d');
      const { left, top, width, height, z, x, y, timeStamp } = this.captureListVideoGuideEvents.shift();

      // canvas.width = image.naturalWidth;
      // canvas.height = image.naturalHeight;

      let tools = LS.getItem('videoEditorTools');
      const videoDrawingToolsEnable = LS.getItem('videoDrawingToolsEnable');

      if (videoDrawingToolsEnable !== 'true') {
        tools = 'cursorRing';
      }

      switch (tools) {
        case 'cursorDefault':
          ctx.drawImage(image, 0, 0);
          ctx.lineWidth = 2;
          ctx.strokeStyle = '#cc0000';
          ctx.rect((left * z) - 5, (top * z) - 5, (width * z) + 10, (height * z) + 10);
          ctx.stroke();
          break;
        case 'cursorShadow': {
          const radialGradient = ctx.createRadialGradient(x * z, y * z, 1, x * z, y * z, 75);
          radialGradient.addColorStop(0, 'rgba(0,0,0,1)');
          radialGradient.addColorStop(0.65, 'rgba(0,0,0,1)');
          radialGradient.addColorStop(1, 'rgba(0,0,0,0)');

          ctx.drawImage(image, 0, 0);
          ctx.globalAlpha = 0.6;
          ctx.fillStyle = '#000';
          ctx.fillRect(0, 0, image.naturalWidth, image.naturalHeight);
          ctx.globalAlpha = 1;
          ctx.globalCompositeOperation = 'destination-out';
          ctx.fillStyle = radialGradient;
          ctx.arc(x * z, y * z, 75, 0, 2 * Math.PI, true);
          ctx.fill();
          ctx.globalCompositeOperation = 'destination-over';
          ctx.drawImage(image, 0, 0);
          break;
        }
        default:
          ctx.drawImage(image, 0, 0);
          ctx.arc(x * z, y * z, 25, 0, 2 * Math.PI, true);
          ctx.globalAlpha = 0.2;
          ctx.fillStyle = '#cc0000';
          ctx.fill();
          ctx.globalAlpha = 1;
          ctx.lineWidth = 2;
          ctx.strokeStyle = '#cc0000';
          ctx.stroke();
          break;
      }

      // eslint-disable-next-line no-restricted-syntax
      for await (const [index, event] of this.listVideoGuideEvents.entries()) {
        if (timeStamp === event.timeStamp) {
          this.listVideoGuideEvents[index].blob = await canvas.convertToBlob();
          // console.log('create blob', event.element, event.event);
        }
      }

      if (this.captureListVideoGuideEvents.length) {
        await this.saveVideoGuideEventCapture();
      } else {
        this.captureVideoGuideProcess = false;

        // console.log('saveEventCapture finish', this.listVideoGuideEvents);

        const { status } = await nscCore.sendMessage({ operation: 'getScreencastStatusOffscreen' });
        if (!status) {
          await AfterCapture.saveGuide();
        }
      }
    } catch (e) {
      console.error(e);
      if (this.captureListVideoGuideEvents.length) { // todo
        await this.saveVideoGuideEventCapture();
      }
    }
  }

  static async saveVideoGuideEvent(param) {
    const { time } = await nscCore.sendMessage({ operation: 'getScreencastStatusOffscreen' });

    const { length } = this.listVideoGuideEvents;
    const lastEvent = this.listVideoGuideEvents[length - 1];

    if (length
      && lastEvent.event === param.event
      && lastEvent.width === param.width
      && lastEvent.height === param.height
      && lastEvent.left === param.left
      && lastEvent.top === param.top) {
      this.listVideoGuideEvents.pop();
      this.captureListVideoGuideEvents.pop();
    }

    this.listVideoGuideEvents.push({ ...param, time, blob: null });
    this.captureListVideoGuideEvents.push({ ...param });

    console.log('saveVideoGuideEvent', this.captureListVideoGuideEvents);

    if (!this.captureVideoGuideProcess) {
      this.captureVideoGuideProcess = true;
      await this.saveVideoGuideEventCapture();
    }
  }

  static getMixedAudioStream(arrayOfAudioStreams) {
    const audioContext = new AudioContext();
    const gainNode = audioContext.createGain();

    const audioSources = [];
    let audioTracksLength = 0;

    gainNode.connect(audioContext.destination);
    gainNode.gain.value = 0;

    arrayOfAudioStreams.forEach(stream => {
      if (!stream.getAudioTracks().length) {
        return;
      }

      audioTracksLength++;

      const audioSource = audioContext.createMediaStreamSource(stream);
      audioSource.connect(gainNode);
      audioSources.push(audioSource);
    });

    if (!audioTracksLength) {
      return;
    }

    const audioDestination = audioContext.createMediaStreamDestination();

    audioSources.forEach(audioSource => {
      audioSource.connect(audioDestination);
    });

    // eslint-disable-next-line consistent-return
    return audioDestination.stream;
  }
}
